<style lang="less">
    @import './login.less';
</style>

<template>
    <div class="login"
         @keydown.enter="handleSubmit">
        <div class="login-con">
            <Card :bordered="false">
                <p slot="title">
                    <Icon type="log-in"></Icon>
                    欢迎登录
                </p>
                <div class="form-con">
                    <Form ref="loginForm"
                          :model="form"
                          :rules="rules">
                        <FormItem prop="userName">
                            <Input v-model="form.userName"
                                   placeholder="请输入用户名">
                            <span slot="prepend">
                                <Icon :size="16"
                                      type="person"></Icon>
                            </span>
                            </Input>
                        </FormItem>
                        <FormItem prop="password">
                            <Input type="password"
                                   v-model="form.password"
                                   placeholder="请输入密码">
                            <span slot="prepend">
                                <Icon :size="14"
                                      type="locked"></Icon>
                            </span>
                            </Input>
                        </FormItem>
                        <FormItem>
                            <Button @click="handleSubmit"
                                    type="primary"
                                    long>登录</Button>
                        </FormItem>
                    </Form>
                    <div class="login-tip">
                        <p>没有账号
                            <router-link to='/register'>前往注册</router-link>
                        </p>
                    </div>
                </div>
            </Card>
        </div>
    </div>
</template>

<script>
import Cookies from 'js-cookie';
import {login} from '../libs/api.js';

import sha1 from 'sha1';
export default {
    data() {
        return {
            form: {
                userName: 'admin',
                password: ''
            },
            rules: {
                userName: [{ required: true, message: '账号不能为空', trigger: 'change' }],
                password: [{ required: true, message: '密码不能为空', trigger: 'change' }],
            },
        };
    },
    methods: {
        handleSubmit() {
            this.$refs.loginForm.validate(async valid => {
                if (valid) {
                    var newpwd = sha1(this.form.password);
                    await login({userName:this.form.userName,password:newpwd}).then(res=>{
                        if (res.data.success) {
                            const userInfo = res.data.data;
                            console.log(userInfo)
                            if (userInfo.is_manager === 1) {
                                Cookies.set('access', 0);
                            } else {
                                Cookies.set('access', 1);
                            }
                            this.$Message.success('登录成功！')
                            localStorage.setItem('userInfo',JSON.stringify(userInfo))
                            Cookies.set('user', userInfo.userName);
                            this.$router.push('/')
                        } else {
                            this.$Message.success(res.data.desc)
                        }
                    },err=>{
                        console.log(err)
                    })
                }
            });
        },
    }
};
</script>

<style>
</style>



